import React, { PureComponent } from "react";
import Styled from "styled-components";
import Section from "components/common/Section";
import grbl from 'assets/report/grbl.png';

const PersonalBadInfo = Styled.div`
    padding: 0.3734rem 0.2667rem;
    color: #5B5B5B;
    font-size: 0.3734rem;
    line-height: 0.5334rem;

    & > div {
        margin-top: 0.24rem;
        padding-bottom: 0.1334rem;
        border-bottom: 1px solid #E6E7EA;

        &:first-of-type {
            margin-top: 0;
        }

        &:last-of-type {
            border-bottom: 0;
        }

        p {
            margin-top: 0.1334rem;
        }

        div {
            margin-top: 0.32rem;

            &:first-of-type {
              margin-top: 0;
            }
        }
 
        .T {
            margin-top: 0;
            padding-bottom: 0.1067rem;
            color: #4EA0FF;
        }

        span {
            color: #8E8E8E;
        }
    }
`;

export default class extends PureComponent {
  render() {
    return (
      <Section>
        <Section.Title>
          <img
            src={grbl}
            alt="GRBL"
            onClick={() => false}
          />
          个人不良信息
        </Section.Title>
        <PersonalBadInfo>
          <div>
            <p className="T">司法案例信息</p>
            <div>
              <p>
                <span>案件类型：</span>
                民事
                  </p>
              <p>克**、拉**纠纷一审民事判决书</p>
              <p>
                <span>审结年份：</span>
                2017年
                  </p>
              <p>
                <span>当事人类型：</span>
                证人
                  </p>
            </div>
          </div>
          <div>
            <p className="T">司法执行信息</p>
            <div>
              <p>詹**危险驾驶罪二审刑事判定书</p>
              <p>
                <span>审结年份：</span>
                2019年
              </p>
              <p>
                <span>执行标的：</span>
                50000
              </p>
            </div>
          </div>
          <div>
            <p className="T">失信信息</p>
            <p>没有与你相关的金融失信信息</p>
          </div>
          <div>
            <p className="T">税务执法信息</p>
            <p>没有与你相关的税务执法信息</p>
          </div>
          <div>
            <p className="T">催欠公告信息</p>
            <div>
              <p>詹**拖欠广东**有限公司贷款***元款项发出催欠律师函</p>
              <p>
                <span>发布日期：</span>
                2018/05/08
              </p>
            </div>
          </div>
          <div>
            <p className="T">网贷逾期信息</p>
            <div>
              <p>詹**涉及网贷逾期*元</p>
              <p>
                <span>发布日期：</span>
                2017/05/08
              </p>
            </div>
          </div>
        </PersonalBadInfo>
      </Section>
    );
  }
}
