import React from "react";
import Styled from "styled-components";
import Section from "components/common/Section";
import bggs from 'assets/report/bggs.png';
import hit from 'assets/report/hit.png';
import miss from 'assets/report/miss.png';

const ReportSummary = Styled.div`
    padding: 0.32rem 0.2667rem;
    color: #5B5B5B;
    font-size: 0.32rem;
    line-height: 0.4534rem;

    p {
        margin-top: 0.2134rem;
        display: flex;
        align-items: center;

        &:first-of-type {
          margin-top: 0;
        }
    }

    img {
        height: 0.4267rem;
        margin-right: 0.2134rem;
        display: block;
    }
`;

export default class extends React.PureComponent {
  render() {
    return (
      <Section>
        <Section.Title>
          <div>
            <div>
              <img
                src={bggs}
                alt="BGGS"
                onClick={() => false}
              />
              报告概述
            </div>
            <span>
              共<em>3</em>条不良记录
            </span>
          </div>
        </Section.Title>
        <ReportSummary>
          <p>
            <img
              src={hit}
              alt="hit"
              onClick={() => false}
            />
            命中羊毛党名单
          </p>
          <p>
            <img
              src={hit}
              alt="hit"
              onClick={() => false}
            />
            命中多头申请记录
          </p>
          <p>
            <img
              src={hit}
              alt="hit"
              onClick={() => false}
            />
            命中个人风险信息
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            当前信用资质等级极好
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            当前信贷需求等级低
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            无个人不良信息
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            无社会不良信息
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            中国移动号码状态正常，在网时长24个月以上
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            年偿债能力不低于3.6万元
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            当前财富收入估值为{parseFloat(3.6 * 1.25).toFixed(1)}万元
          </p>
        </ReportSummary>
      </Section>
    );
  }
}
