import React from "react";
import Styled from "styled-components";
import Section from "components/common/Section";
import jrxw from 'assets/report/jrxw.png';

const FinancialBehavior = Styled.div`
    padding-top: 0.5334rem;
    display: flex;
    flex-direction: column;
`;

FinancialBehavior.Grade = Styled.div`
    padding: 0 1.0667rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    span {
        color: #5B5B5B;
        font-size: 0.3734rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        em {
            margin-top: 0.08rem;
            font-size: 0.7467rem;
            color: ${({ score }) => {
    if (score > 80) {
      return "#E73B11";
    } else if (score > 60) {
      return "#E6A200";
    } else if (score > 40) {
      return "#5CAA07";
    } else {
      return "#4EA0FF";
    }
  }};
        }
    }

    .sp {
        width: 0.0267rem;
        height: 1.28rem;
        background-color: #D8D8D8;
    }
`;

FinancialBehavior.Progress = Styled.div`
    margin-top: 0.4267rem;

    .pC {
        height: 0.32rem;
        background-color: #D8D8D8;
    }

    .grD {
        margin-top: 0.08rem;
        color: #9B9B9B;
        font-size: 0.32rem;
        display: flex;
        justify-content: space-between;

        span {
            color: #E73B11;
        }
    }
`;

FinancialBehavior.Progress.Highlight = Styled.div`
    width: ${({ score }) => {
    return `${score}%`;
  }};
    height: 100%;
    background-color: ${({ score }) => {
    if (score > 80) {
      return "#E73B11";
    } else if (score > 60) {
      return "#E6A200";
    } else if (score > 40) {
      return "#5CAA07";
    } else {
      return "#4EA0FF";
    }
  }};
`;

FinancialBehavior.Tip = Styled.p`
    margin-top: 0.2667rem;
    padding: 0 1.6rem;
    color: #B8B5B5;
    font-size: 0.32rem;
    line-height: 0.4534rem;
    text-align: center;
`;

FinancialBehavior.Read = Styled.div`
    padding-bottom: 0.5334rem;
    color: #8E8E8E;
    font-size: 0.32rem;
    line-height: 0.4534rem;
    border-top: 1px solid #E6E7EA;

    .T {
        padding: 0.2667rem 0 0.08rem;
        color: #8E8E8E;
        font-size: 0.3734rem;
        line-height: 0.5334rem;
    }

    li {
        padding-left: 0.16rem;
        position: relative;

        &:before {
            content: '·';
            left: 0;
            position: absolute;
        }
    }
`;

FinancialBehavior.Statistics = Styled.div`
    .T {
        padding: 0.2134rem 0.2667rem;
        color: #4EA0FF;
        font-size: 0.3734rem;
        border-top: 1px solid #E6E6E6;  
        display: flex;
        justify-content: space-between;

        span {
            color: #9B9B9B;
            font-size: 0.32rem;
        }
    }

    ul {
        padding: 0.24rem 0.2667rem 0.32rem;

        li {
            margin-top: 0.2667rem;
            color: #8E8E8E;
            font-size: 0.3734rem;
            line-height: 0.64rem;
            display: flex;
            justify-content: space-between;

            &:first-of-type {
                margin-top: 0;
            }

            span {
                color: #5B5B5B;
            }
        }
    }
`;

function getLevel(score) {
  if (score > 80) {
    return "非常高";
  } else if (score > 60) {
    return "高";
  } else if (score > 40) {
    return "一般";
  } else if (score > 20) {
    return "低";
  } else {
    return "非常低";
  }
}

export default class extends React.PureComponent {
  render() {
    const score = 85;
    return (
      <Section>
        <Section.Title>
          <img
            src={jrxw}
            alt="JRXW"
            onClick={() => false}
          />
          金融行为分析
        </Section.Title>
        <FinancialBehavior>
          <div style={{ padding: "0 0.2667rem" }}>
            <FinancialBehavior.Grade score={score}>
              <span>
                信贷需求评分
                <em className="sC">{score}</em>
              </span>
              <div className="sp" />
              <span>
                信贷需求等级
                <em className="lvL">{getLevel(score)}</em>
              </span>
            </FinancialBehavior.Grade>
            <FinancialBehavior.Progress>
              <div className="pC">
                <FinancialBehavior.Progress.Highlight score={score} />
              </div>
              <div className="grD">
                非常低<span>非常高</span>
              </div>
            </FinancialBehavior.Progress>
            <FinancialBehavior.Tip>
              信贷指数是通过科学的算法计算得出，反映用户近期资金需求程度仅供参考
            </FinancialBehavior.Tip>
            <div style={{ marginTop: "0.5334rem" }}>
              <Section.Item>
                多头信贷申请次数<span>8次</span>
              </Section.Item>
              <Section.Item>
                网络贷款机构申请次数<span>10次</span>
              </Section.Item>
              <Section.Item>
                消费金融机构申请次数<span>5次</span>
              </Section.Item>
              <Section.Item>
                投资活跃评分<span className="sCe">58分</span>
              </Section.Item>
              <Section.Item>
                信贷活跃评分<span className="sCe">58分</span>
              </Section.Item>
              <FinancialBehavior.Read>
                <p className="T">活跃评分解读</p>
                <ul>
                  <li>投资习惯一般，偏好证券、保险类投资，投资收益有一定波动</li>
                  <li>借贷习惯良好，无固定借贷产品偏好；借贷行为变动较大，曾集中申请信用卡、P2P类产品</li>
                </ul>
              </FinancialBehavior.Read>
            </div>
          </div>
          <FinancialBehavior.Statistics>
            <div className="T">
              机构查询次数<span>时间范围：近24月</span>
            </div>
            <ul>
              <li>
                商业银行<span>8</span>
              </li>
              <li>
                支付机构<span>2</span>
              </li>
              <li>
                小额贷款<span>5</span>
              </li>
              <li>
                互联网金融<span>2</span>
              </li>
              <li>
                消费金融公司<span>2</span>
              </li>
              <li>
                汽车金融公司<span>1</span>
              </li>
              <li>
                互联网服务公司<span>1</span>
              </li>
              <li>
                电子商务公司<span>2</span>
              </li>
              <li>
                其他<span>4</span>
              </li>
              <li>
                合计<span>27</span>
              </li>
            </ul>
          </FinancialBehavior.Statistics>
          <FinancialBehavior.Statistics>
            <div className="T">信贷行为统计</div>
            <ul>
              <li>
                近3个月信贷放款笔数<span>7笔</span>
              </li>
              <li>
                近6个月信贷放款笔数<span>8笔</span>
              </li>
              <li>
                近12个月信贷放款笔数<span>9笔</span>
              </li>
              <li>
                信贷机构总数<span>5家</span>
              </li>
              <li>
                贷款总笔数<span>6笔</span>
              </li>
              <li>
                平均放款额度<span>¥7503.58</span>
              </li>
            </ul>
          </FinancialBehavior.Statistics>
        </FinancialBehavior>
      </Section>
    );
  }
}
